import React from 'react';
import styled from 'styled-components'


const JoinSectionBlock=styled.div`
    height:90vh;
    padding: 100px 0;
    display:flex;
    align-items:center;
    justify-content:center;
    flex-direction:column;
    h2{
        font-size:28px;
        margin-bottom: 30px;
    }
    .outerformbox{
        padding:50px 0 ;
        display:flex;
        align-items:center;
        justify-content:center;
        width: 100%;
        background:#fffaf4;
        .innerformbox{
            width:400px;
            display:flex;
            flex-direction:column;
            input{
                height:50px;
                margin: 10px;
                border:none;
                border-bottom:1px solid black;
                padding-left:10px;
                background:transparent;
            }
            button{
                cursor:pointer;
                position:relative;
                margin-top:30px;
                height:50px;
                border:1px solid black;
                background:none;
                transition:all 0.3s;
                &:hover{
                    color:white;
                    background:black;
                }
            }
        }
    }
`;

const JoinSection = () => {
    return (
        <JoinSectionBlock>
                    <h2>Join</h2>
           <div className='outerformbox'>
                <form className='innerformbox'>
                    <input type="text" placeholder='Email (include @)'/>
                    <input type="text" placeholder='ID or Membership Number'/>
                    <input type="password" placeholder="Password"/>
                    <input type="password" placeholder="Password OK"/>
                    <input tpye="password" placeholder="Phone Number (except ' - ')"/>
                    <button>Join-Now</button>
                </form>
           </div>
        </JoinSectionBlock>
    );
};

export default JoinSection;