import React from 'react';
import styled from 'styled-components'
import { Link } from 'react-router-dom';


const LoginSectionBlock = styled.div`
height:90vh;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center; 
    h2{
        font-size:28px;
        margin-bottom:30px;
    }
    .outerFormBox{
        width:100%;
        text-align:center;
        .innerFormBox{
            padding:50px;
            background:white;
            input{
                width:300px; 
                height:50px;
                margin: 10px;
                border:none;
                border-bottom:1px solid black;
                padding-left:10px;
            }
            button{
                cursor:pointer;
                position:relative;
                margin-top:20px;
                width:300px;
                height:50px;
                border:1px solid black;
                background:none;
                transition:all 0.3s;
                &:hover{
                    color:white;
                    background:black;
                }
                
            }
        }
    }
    .help{
        padding-top:30px;
        p{
            padding:10px 0;
            text-align:center;
            &.linktag{
                a{
                    position: relative;
                    font-weight:normal;
                    &::after{
                        position:absolute;
                        content:'';
                        top:100%;
                        left:0;
                        width:0%;
                        height:1px;
                        background: black;
                        transition:all 0.3s;
                    }
                    &:hover{
                        &::after{
                            width:100%;
                        }
                    }
                }
            }
        }

    }
`;


const LoginSection = () => {
    return (
        <LoginSectionBlock>
            <h2>Log in</h2>
            <div className='outerFormBox'>
                <div className='innerFormBox'>
                    <form action="" type="post">
                        <p><input placeholder='ID or Membership Number'/></p>
                        <p><input placeholder='Password'/></p>
                        <button>Login</button>
                    </form>
                </div>
            </div>
            <div className='help'>
                <p>Aren't you a member yet?</p>
                <p className='linktag'><Link to="/Join">Join</Link></p>
                <p>Have you forgotten your ID or PW?</p>
                <p className='linktag'><Link to="/">Find ID</Link></p>
                <p className='linktag'><Link to="/">Find PW</Link></p>
            </div>            
        </LoginSectionBlock>
    );
};

export default LoginSection;