import React, {useState, useEffect} from 'react';
import styled from 'styled-components'
import {Link} from 'react-router-dom';

const Nav = styled.div`

position:fixed;
z-index:99;
padding-top: 50px;
    width: 100%;
    color:black;
    font-weight:bold;
    .logo{
        width: 100px;
        margin: 0 auto;
        img{
            cursor:pointer;
        }
    }
    .depth{
        position:absolute;
        top:50px;
        left: 100px;
        li{ 
            position:relative;
            margin: 5px; 0;
            &:hover{
                &::after{
                    width:100%;;
                }
            }
            &::after{
                position:absolute;
                content:'';
                top:110%;
                left:0;
                width:0px;
                height:1px;
                background: #000;
                transition:all 0.3s;
            }
            a{
                transition: all 1s;
            }
        }
    }    
    .topmenu{
        width:200px;
        position: absolute;
        right: 50px;
        top:0;
        display:flex;
        background: rgba(0,0,0,0.05);
        justify-content:space-between;
            a{
                color:gray;
                text-align:center;
                width:40%;
                padding-bottom:5px;
                transition:all 0.3s;
                &:hover{
                    color:black;
                }
            }
        
    }
    &.white{
        .depth{
            li{
                a{
                    color:white;
                }
                &::after{
                    background:white;
                }
            }
        }
        .topmenu{
            a{
                color: white;
                &:hover{
                    color: black;
                }
            }
        }
    }
`;
    
    const Header = ({color}) => {
        
        const scrollBlack = window.innerHeight
        // console.log(scrollBlack)
        const [scrollcolor,setScrollColor]=useState(100);
    const onScroll = ()=>{
        setScrollColor(window.scrollY)
    }
    
    useEffect(()=>{
        window.addEventListener('scroll',onScroll)
        return ()=> window.removeEventListener('scroll',onScroll)
    },[]
    )

    if(scrollcolor<scrollBlack-150 && color==='white'){
        return(
        <Nav className='white'>
            <h1 className='logo'><Link to="/"><img src="img/logo.png" alt="x"/></Link></h1>
            <ul className='depth'>
                <li><Link to="/">Our Story</Link></li>
                <li><Link to="/Location">Location</Link></li>
                <li><Link to="/Register">Reservation</Link></li>
            </ul>
            <div className='topmenu'>
                <Link to="/Login">Log-in</Link>
                <Link to="/Join">Join</Link>
            </div>
        </Nav>  
        )
    }
        return (
            <Nav>
            <h1 className='logo'><Link to="/"><img src="img/glow-icon.png" alt="x"/></Link></h1>
            <ul className='depth'>
                <li><Link to="/">Our Story</Link></li>
                <li><Link to="/Location">Location</Link></li>
                <li><Link to="/Register">Reservation</Link></li>
            </ul>
                    <div className='topmenu'>
                        <Link to="/Login">Log-in</Link>
                        <Link to="/Join">Join</Link>
                    </div>

            </Nav>
        );
    // }


};

export default Header;