import React, {useState, useEffect}  from 'react';
import styled from 'styled-components'
import {Link} from 'react-router-dom'
import axios from 'axios'
import {FaLocationArrow} from 'react-icons/fa'
import AOS from 'aos';
import 'aos/dist/aos.css';
import cn from 'classnames';

const WeatherBoxBlock = styled.div`
padding: 50px 0 10px;
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
    .weatherBox{
        background:linear-gradient(to right bottom, #a6d1e2, white);
        box-shadow: 10px 5px 15px 3px rgba(0,0,0,0.2);
        border-radius: 10px;
        padding: 80px 100px 50px;
        width:500px;
        position:relative;
        h2{
            position:absolute;
            top:10px;
            left:10px;
            padding-bottom:30px;
            font-size: 17px;
            text-align:left;
            // border:1px solid red;
        }
        >div{
            width:100%;
            display:flex;
            align-items:center;
            justify-content:space-between;
            >div{
                display:flex;
                align-items: center;
            }
            &.now{
                div{
                    align-items:flex-start;
                    padding-bottom:30px;
                    span{
                        font-size:52px;
                    }
                }
            }
            &.temp{
                div{
                    padding-bottom:30px;
                    flex-direction:column;
                }
            }
            &.wind{
                // border:1px solid black;
                div{
                    flex-direction:column;
                    padding:10px 0;
                    display:flex;
                    justify-content:center;
                    width:45%;
                    // border:1px solid black;
                    strong{
                        margin-bottom:10px;
                    }
                    span{
                        display:flex;
                        align-items:center;
                    }
                }
            }
        }
        button{
            position:absolute;
            bottom:10px;
            right:10px;
            border:1px solid black;
            padding: 5px 10px;
            background:none;
            border-radius: 10px;
            cursor:pointer;
            transition:all 0.3s;
            &:hover{
                background:black;
                color:white
            }
        }
    }
    .weekWeatherBox{
        position:absolute;
        top:650px;
        background:linear-gradient(to right bottom, #a6d1e2, white);
        min-width:500px;
        box-shadow: 10px 5px 15px 3px rgba(0,0,0,0.2);
        border-radius: 10px;
        z-index:1;
        transition:all 0.5s;
        opacity:0;
        table{
            width:600px;
            margin: 20px;
            text-align:center;
            colgroup{
                col{
                    &:nth-child(1){width:20%;}
                    &:nth-child(2){width:30%;}
                    &:nth-child(3){width:15%;}
                    &:nth-child(4){width:20%;}
                    &:nth-child(5){width:15%;}
                }
            }
            thead{
                tr{
                    th{border:1px solid black; padding:5px;}
                }
            }
            tbody{
                tr{
                    td{border:1px solid black; padding:5px;}
                }
            }
        }
        &.on{
            opacity:1;
        }
    }

`;

const Weatherbox = () => {

    const[Temps,setTemp]=useState([])
    const[Weather,setWeather]=useState([])
    const[Wind,setWind]=useState([])
    // console.log(Temps)
    const todayWeather = async ()=>{
        const response1 = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=seoul&units=metric&appid=5eb107829897c53a70f4025f453107d9`);
        // console.log(response.data.weather)
        setTemp(response1.data.main)
        setWeather(response1.data.weather[0])
        setWind(response1.data.wind)
    }

    // const weather = Weather.main
    // const temp = Temps.temp
    // const temp_min = Temps.temp_min
    // const temp_max = Temps.temp_max
    // const feels_like = Temps.feels_like
    // const wind = Wind.speed
    // const windDeg = Wind.deg
    useEffect(()=>{todayWeather()},[])

    
    const today = new Date
    const month = today.getMonth()+1
    const date = today.getDate()
    
    
    var nowmonth ='';
    switch(month){
        case 1 : nowmonth='Jan.' ;break;
            case 2 : nowmonth='Feb.' ;break;
            case 3 : nowmonth='Mar.'; break;
            case 4 : nowmonth='Apr.' ;break;
            case 5 : nowmonth='May' ;break;
            case 6 : nowmonth='Jun.' ;break;
            case 7 : nowmonth='Jul.' ;break;
            case 8 : nowmonth='Aug.' ;break;
            case 9 : nowmonth='Sep.' ;break;
            case 10 : nowmonth='Oct.' ;break;
            case 11 : nowmonth='Nov.' ;break;
            case 12 : nowmonth='Dec.' ;break;
        }
    var nowdate ='';
    switch(date){
        case 1 :    nowdate=date+'st';break;
        case 2 :    nowdate=date+'nd';break;
        case 3 :    nowdate=date+'rd';break;
        case 21 :   nowdate=date+'st';break;
        case 22 :   nowdate=date+'nd';break;
        case 23 :   nowdate=date+'rd';break;
        case 31 :   nowdate=date+'st';break;
        default :   nowdate=date+'th';break;
    }
    
    
    
    // const[day1,setDay1]=useState([])
    // const[day1Weather,setDay1Weather]=useState([])
    // const[day1Temp,setDay1Temp]=useState([])
    // const[day1Rain,setDay1Rain]=useState([])
    // const[day1Wind,setDay1Wind]=useState([])
    // const[day2,setDay2]=useState([])
    // const[day2Weather,setDay2Weather]=useState([])
    // const[day3,setDay3]=useState([])
    // const[day3Weather,setDay3Weather]=useState([])
    // const[day4,setDay4]=useState([])
    // const[day4Weather,setDay4Weather]=useState([])
    











    const [listUp,setListUp]=useState([]);
    
    var indexListNum=9
    var indexList=[];

    const weekWeather = async ()=>{
        const response2 = await axios.get(`https://api.openweathermap.org/data/2.5/forecast?q=seoul&units=metric&cnt=35&appid=5eb107829897c53a70f4025f453107d9`);
        // console.log(response2.data.list)

        for(let i=0;i<4; i++){
            indexList[i]=response2.data.list[indexListNum]
            indexListNum+=8
        }
        console.log(indexList[0])
        setListUp(listUp.concat(indexList))


        // setDay1(response2.data.list[9])
        // setDay1Weather(response2.data.list[9].weather[0])
        // setDay1Temp(response2.data.list[9].main)
        // setDay1Rain(response2.data.list[9].rain)
        // setDay1Wind(response2.data.list[9].wind)

        // setDay2(response2.data.list[17])
        // setDay2Weather(response2.data.list[17].weather[0])

        // setDay3(response2.data.list[25])
        // setDay3Weather(response2.data.list[25].weather[0])
        
        // setDay4(response2.data.list[33])
        // setDay4Weather(response2.data.list[33].weather[0])
    }


    useEffect(()=>{weekWeather()},[])
    useEffect(()=>{AOS.init({duration:1000})})
    const[on,setOn]=useState(false)
    
        return (
            <WeatherBoxBlock>
                <div className='weatherBox' data-aos="fade-zoom-in" data-aos-easing="ease-in-back" data-aos-offset="0">
                    <h2>( {nowmonth} {nowdate} ) Seoul, Republic of Korea</h2>
                        <div className='now' >
                            <div><span style={{paddingRight:'20px' ,fontSize: '42px'}}>{Weather.description}</span></div>
                            <div><span>{Temps.temp}</span></div>
                        </div>
                        <div className='temp'>
                            <div><strong>LOWS</strong> <span>{Temps.temp_min}</span></div>
                            <div><strong>MAX</strong> <span>{Temps.temp_max}</span></div>
                            <div><strong>WIND CHILL</strong> <span>{Temps.feels_like}</span></div>
                        </div>
                        <div className='wind'>
                            <div><strong>WIND SPEED</strong> <span>{Wind.speed} m/s</span></div>
                            <div><strong>WIND DEGREE</strong> <span><FaLocationArrow style={{transform:`rotate(${Wind.deg-45}deg)`,fontSize:'18px', marginRight:'10px'}}/> {Wind.deg}</span></div>
                        </div>
                        <button type="button" onClick={()=>{ setOn(!on) }}>See More Weather</button>
                    </div>
                    <div className={cn('weekWeatherBox',{on})}>
                        
                            <table>
                                <colgroup>
                                <col/>
                                <col/>
                                <col/>
                                <col/>
                                <col/>
                                <col/>
                                </colgroup>
                            <thead>
                                <tr>
                                    <th>Date</th>
                                    <th>Weather</th>
                                    <th>Temp</th>
                                    <th>Humidity</th>
                                    <th>Wind</th>
                                </tr>
                            </thead>

                            <tbody>
                                {listUp && listUp.map((data,index)=>(
                                <tr key={index}>
                                    <td>{data.dt_txt}</td>
                                    <td>{data.weather[0].description}</td>
                                    <td>{data.main.temp}</td>
                                    <td>{data.main.humidity} %</td>
                                    <td>{data.wind.speed} </td>
                                </tr>
                                ))
                                }
                            </tbody>

                        </table>
                    </div>
            </WeatherBoxBlock>
    );
};

export default Weatherbox;