import React from 'react';
import styled from 'styled-components'

const FooterBlock=styled.div`
    padding:30px;
    background:#fffaf4;
    .footerbox{
        display:flex;
        justify-content:space-between;
        align-items:center;
        span{
            display:inline-block;
            width:33%;
            text-align:center;
            font-size: 15px;
            font-weight:normal;
        }
    }
`;


const Footer = ({color}) => {
    return (
        <FooterBlock style={{background: color && 'white'}}>
            <div className='footerbox'>
            <span>
            clone coding origin by https://dribbble.com/shots/following/web-design
            </span>
            <span>
                Company.
            </span>
            <span>
                RyuKevin
            </span>
            </div>
        </FooterBlock>
    );
};

export default Footer;