import React,{useEffect} from 'react';
import {Link} from 'react-router-dom'
import styled from 'styled-components'
import{FaLongArrowAltRight} from 'react-icons/fa'
import AOS from 'aos';
import 'aos/dist/aos.css';


const ContentBlock =styled.div`

.VideoBox{
    position:relative;
    width:100%;
    height:100vh; 
    overflow:hidden;
    video{
        pointer-events: none;
        min-width:100%;
        min-height:100%;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, -50%);
    }
}

    .contentbox{
        >div{
            padding:100px 50px;
        }
        // img{max-width:1000px;}
        .content1{
            .upbox{
                overflow:hidden;
                height:50%;
                display:flex;
                justify-content:space-between;
                .leftbox{
                    align-self:center;
                    width:40%;

                    h1{
                        font-size:45px;
                        margin-bottom:10px;
                    }
                    p{
                        line-height: 25px;
                        padding-bottom:20px;
                    }
                    .rglink{
                        span{
                            position:relative;
                            &::after{
                                content:'';
                                position:absolute;
                                bottom:-5px;
                                left:0;
                                width:100%;
                                height:2px;
                                background:black;
                                transition:all 0.3s;
                            }
                        }
                        &:hover >span::after{
                            width:0;
                        }
                    }
                }
                .rightbox{
                    width: 48%;
                }        
            }
            .downbox{
                padding:70px 0;
                text-align:center;
            }
        }

        .content2{
            background:#fffaf4;
            // padding:80px;
            .box1{
                overflow:hidden;
                margin-bottom:150px;
                display:flex;
                justify-content:space-between;
                .leftbox{
                    width:300px;
                    // border:1px solid black;
                }
                .rightbox{
                    width:60%;
                    align-self:flex-end;
                    ul{
                        
                        li{
                            
                            // border:1px solid #000;
                            font-size: 38px;
                            display:flex;
                            margin-bottom: 60px;
                            .num{
                                margin:10px 30px 0 0;
                                height:60px;
                                padding:0 25px;
                                line-height:55px;
                                font-size: 20px;
                                border:1px solid #000;
                                border-radius: 50%;
                            }
                            .ment{
                                word-break: keep-all;
                                font-weight:normal;
                            }
                        }
                    }
                }
            }
            .box2{
                overflow:hidden;
                display:flex;
                justify-content:space-between;
                .leftbox{
                    width:45%;
                    // padding-left:100px;
                    overflow:hidden;
                    img{
                        transform:scale(1.1);
                        // height:350px;
                    }
                }
                .rightbox{
                    width:40%;
                    // padding-right:200px;
                    h3{
                        margin-bottom:20px;
                    }
                    span{
                        font-size: 25px;
                        line-height:35px;
                    }
                }
            }
        }
        .content3{
            .imgbox{text-align:center;}
            .sendbox{
                padding-top:80px;
                .pbox{
                    h2{
                        font-size:45px;
                        margin-bottom:10px;
                    }
                    p{
                        line-height: 25px;
                        &:last-child{
                            padding-bottom:10px;
                        }
                    }
                }
                .formbox{
                    p{
                        select{
                            height:35px;
                            width:250px;
                            margin-bottom:20px;
                            border:none;
                            border-bottom:1px solid #000;
                            &:first-child{
                                margin-right:20px;
                            }
                            &.long{
                                width:520px;
                            }
                        }
                        input{
                            &.box{
                                height:35px;
                                width:250px;
                                margin-bottom:20px;
                                border:none;
                                border-bottom:1px solid #000;
                                &:first-child{
                                    margin-right:20px
                                }
                            }
                        }
                        label{
                            font-size: 13px;
                            margin:0 20px 0 5px;;
                        }
                        &.checkbox1{
                            margin:10px 0;

                        }
                    }
                    button{
                        margin-top:20px;
                        background: white;
                        border:1px solid black;
                        padding: 10px 20px;
                        .icon{
                            margin-left:15px;
                            font-size:15px;
                            vertical-align:middle;
                        }
                    }
                }
            }
        }
    }
`;


const Content = () => {

    useEffect(
        ()=>{
        AOS.init({duration:1000})
        }
    )
    



    return (
        
        <ContentBlock>
            <div className="VideoBox">
                <video src="video/intro-video.mp4" title =""autoPlay="yes" muted="muted" loop="yes"  preload="metadata" controls="no" className="lazy-loaded"></video>
            </div>
            <div className="contentbox">
                <div className='content1 row'>
                    <div className="upbox">
                        <div className='leftbox' data-aos="fade-right" data-aos-offset="300" data-aos-easing="ease-in-sine">
                            <h1>Collingwood will soon be home to glow condos</h1>
                            <p>Glow is an iconic new development that marries northern charm with modern luxury. Perfectly placed between the Georgian Bay and Blue Mountains, Glow adds brightness to Collingwoods most coveted neighbourhood.</p>
                            <Link to="" className='rglink'><span>Register</span> now</Link>
                        </div>
                        <div className='rightbox'  data-aos="fade-left" data-aos-offset="300" data-aos-easing="ease-in-sine">
                            <img src="img/glow-interior-1.jpg" alt="x"></img>
                        </div>
                    </div>
                    <div className="downbox" data-aos="fade-up" data-aos-duration="5000">
                        <img src="img/hero-1.jpg" alt="x"></img>
                        </div>
                </div>
                <div className="content2">
                        <div className='box1 row'>
                            <div className="leftbox" data-aos="fade-down" data-aos-easing="linear" data-aos-duration="1500">
                                <p>LARGER THAN LIFE</p>
                            </div>
                            <div className="rightbox">
                                <ul>
                                    <li data-aos="fade-right" data-aos-offset="300" data-aos-easing="ease-in-sine">
                                        <div className="num">1</div>
                                        <div className="ment">Scenic waterfront views and upscale amenities</div>
                                    </li>
                                    <li data-aos="fade-left" data-aos-offset="300" data-aos-easing="ease-in-sine">
                                        <div className="num">2</div>
                                        <div className="ment">Unrivalled living overlooking the Georgian Bay</div>
                                    </li>
                                    <li data-aos="fade-right" data-aos-offset="300" data-aos-easing="ease-in-sine">
                                        <div className="num">3</div>
                                        <div className="ment">A brilliant collection of luxurious suites</div>
                                    </li>
                                    <li data-aos="fade-left" data-aos-offset="300" data-aos-easing="ease-in-sine">
                                        <div className="num">4</div>
                                        <div className="ment">Masterfully designed to evoke a sense of elegance, simplicity and joy</div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    <div className="box2 row">
                        <div className="leftbox" data-aos="fade-right" data-aos-offset="300" data-aos-easing="ease-in-sine">
                        <img src="img/LOBBY_HALL_EXTERIOR_CAM20_crop01_FINAL-min-scaled.jpg" alt="x"></img>
                        </div>
                        <div className="rightbox" data-aos="fade-left" data-aos-offset="300" data-aos-easing="ease-in-sine">
                            <h3>LOCATION</h3>
                            <span>Only a short distance from Blue Mountain, the Collingwood Harbour, several prestigious ski clubs, scenic trails, the Scandinave Spa and seven spectacular golf courses.</span>
                        </div>
                    </div>
                </div>
                <div className="content3 row">
                    <div className="imgbox" data-aos="fade-zoom-in" data-aos-easing="ease-in-back" data-aos-delay="500" data-aos-offset="0">
                        <img src="img/exterior-img-1.jpg" alt="x"></img>
                    </div>
                    <div className="sendbox">
                        <div className='pbox'>
                            <h2>Register your interest</h2>
                            <p>Be one of the sophisticated few to secure this unique</p>
                            <p>expression of elegant Collingwood living.</p>
                        </div>
                        <form className='formbox'>
                            <p className='typingbox'>
                                <input type="text"  name="first_name" placeholder='First Name' className="box"/>
                                <input type="text" name="last_name" placeholder='Last Name' className="box"/>
                            </p>
                            <p className="typingbox">
                                <input type="text" name="email" placeholder='Email' className="box"/>
                                <input type="text" name="hp" placeholder='Phone Number' className="box"/>
                            </p>
                            <p>
                            <select name="servay" className="box long">
                                <option value="none">How did you hear about us?</option>
                                <option value="Spcoal Media">Spcoal Media</option>
                                <option value="Signage">Signage</option>
                                <option value="Other">Other</option>
                            </select>
                            </p>
                            <p>
                            <select name="footage" className="box">
                                <option value="none">Select desired square footage</option>
                                <option value="600-1000 sq ft.">600-1000 sq ft.</option>
                                <option value="1000-1500 sq ft.">1000-1500 sq ft.</option>
                                <option value="1500 + sq ft.">1500 + sq ft.</option>
                            </select>
                            <select name="suite_size" className="box">
                                <option value="none">Select suite size</option>
                                <option value="1+ den">1+ den</option>
                                <option value="2 bedroom">2 bedroom</option>
                                <option value="3 bedroom">3 bedroom</option>
                            </select>
                            </p>
                            <p className='checkboxtit'>ARE YOU A REALTOR?</p>
                            <p className='checkbox1'>
                                <input type="radio" name="realtor" id="yes"/>
                                <label for="yes">YES</label>
                                <input type="radio" name="realtor" id="no"/>
                                <label for="no">NO</label>
                            </p>
                            <p className="checkbox2">
                                <input type="checkbox" name="no" id="receiving_email"/>
                                <label for="receiving_email">I CONSENT TO RECEIVING EMAIL</label>
                            </p>
                            
                            <button type="submit"> Submit <FaLongArrowAltRight className="icon"/></button>
                        </form>
                    </div>
                </div>

            </div>

        </ContentBlock>
    );
};

export default Content;