import React, {useEffect} from 'react';
import styled from 'styled-components'
import AOS from 'aos';
import 'aos/dist/aos.css';


const LocationSectionBlock=styled.div`
min-height:90vh;
padding-top: 150px;
background:#fffaf4;
display: flex;
justify-content:center;
align-items:center;
flex-direction: column;
.BgBox{
    h2{
        font-size:50px;
        position:absolute;
        bottom:0%;
        transform:translate(-50%, -50%);
        left:50%;
        z-index:1;
    }
    width:100%;
    height: 300px;
    display:flex;
    position:relative;
        .bg{
            width:33%;;
            flex:1;
            background-position:center;
            background-size:cover;
            
        }
        .bg1{
            background-image: url(img/exterior-img-1.jpg);
        }
        .bg2{
            background-image: url(img/hero-1.jpg)
        }
        .bg3{
            background-image: url(img/glow-interior-1.jpg)
        }
        .bgdark{
            position:absolute;
            top:0;
            left:0;
            width:100%;
            height:100%;
            background:rgba(255,255,255,0.7)
        }
}
.outerMapBox{
margin-top:50px;
    width:100%;
    .innerMapBox{
        margin: 0 auto;
        width:1000px;
    }
    .location_info{
        padding:20px 0;
        p{
            font-size:20px;
            text-align:center;
            padding-bottom:10px;
        }
        span{
            font-weight:normal;
            display:block;
            text-align:center;
            margin-bottom:20px;
        }
    }
}
`;

const LocationSection = () => {

    useEffect(
        ()=>{
        AOS.init({duration:1000})
        }
    )

    return (
        <LocationSectionBlock>
            <div className='BgBox'>
                <h2>Location</h2>
                    <div className="bg bg1"></div>
                    <div className="bg bg2"></div>
                    <div className="bg bg3"></div>
                    <div className='bgdark'></div>
            </div>
            <div className='outerMapBox' data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-offset="0">
                <div className='innerMapBox'>
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3165.0522611814217!2d127.01743735174519!3d37.50668552971014!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x357ca3e004754641%3A0x5c0b1a9757710032!2z7ISc7Jq47Yq567OE7IucIOyEnOy0iOq1rCDso7ztnaXquLggMzEtNw!5e0!3m2!1sko!2skr!4v1647497135203!5m2!1sko!2skr" width="100%" height="450" style={{border:0}}  loading="lazy" title="">
                    </iframe>
                </div>
                <div className='location_info'>
                    <p>Address</p>
                    <span>123 St. , GangNam-ro , Seocho-gu , Seoul, Republic of Korea</span>
                    <p>Service Center</p>
                    <span> 1234 - 5678</span>
                    <p>Hours of Operation </p>
                    <span>Everyday , AM 0900 - PM 1800</span>
                </div>
            </div>
        </LocationSectionBlock>
    );
};

export default LocationSection;