import React,{useState, useEffect} from 'react';
import styled from 'styled-components'
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import AOS from 'aos';
import 'aos/dist/aos.css';


const ReservationBoxBlock = styled.div`
.reservation{
    margin-bottom:30px;
    h2{
        text-align:center;
    }
    form{
        display:flex;
        flex-direction:column;
        input{
            width: 300px;
            height:40px;
            padding-left:10px;
            margin: 10px 0;
            border:none;
            border-bottom: 1px solid black;
        }
        .room{
            margin: 20px 0;
            padding:10px;
        }
        .reservation{
            margin-top:30px;
            padding:15px;
            font-size:16px;
            background:none;
            border:1px solid black;
            cursor:pointer;
            transition: all 0.3s;
            &:hover{
                background:black;
                color:white;
            }
        }
    }
}
`;

const ReservationBox = () => {
    
    const [startDate, setStartDate] = useState(new Date());
    useEffect(
        ()=>{
        AOS.init({duration:1000})
        }
    )

    return (
        <ReservationBoxBlock>
            <div className='reservation' data-aos="fade-zoom-in" data-aos-easing="ease-in-back" data-aos-delay="100" data-aos-offset="0">
                <h2>Reservation</h2>
                <form>
                    <input placeholder='ID or Membership Number'/>
                    <input placeholder='First Name'/>
                    <input placeholder='Last Name'/>
                    <input placeholder='Phone Number'/>
                    <select className='room'>
                        <option>Choose Room Type</option>
                        <option>Sweet room(ocean view)</option>
                        <option>Sweet room(mountain view)</option>
                        <option>Royal Sweet room(ocean view)</option>
                        <option>Pant House(ocean view)</option>
                    </select>
                    <p>Select Reservation Date</p>
                    <DatePicker selected={startDate} onChange={(date:Date) => setStartDate(date)} />
                    <button className='reservation'>Reservation Now</button>
                </form>
            </div>
        </ReservationBoxBlock>
    );
};

export default ReservationBox;